import {
  REGISTER_SUCCESS,
  //REGISTER_FAIL,
  USER_LOADED,
  AUTH_ERROR,
  LOGIN_SUCCESS,
  //LOGIN_FAIL,
  LOGOUT,
  ACCOUNT_DELETED,
  AFFILIATE_REGISTER_SUCCESS,
  PENDING_AFFILIATE_LOADED,
  UPDATE_AFFILIATE_CONNECTED_ACCOUNT,
} from '../actions/types'

const initialState = {
  token: localStorage.getItem('token'),
  isAuthenticated: null,
  loading: true,
  user: null,
  affiliateIsRegistered: false,
  connectURL: "",
  pendingAffiliate: {},
  affiliateConnectedAccountUpdateLink: '',
}

function authReducer(state = initialState, action) {
  const { type, payload } = action

  switch (type) {
    case USER_LOADED:
      return {
        ...state,
        isAuthenticated: true,
        loading: false,
        user: payload
      }
    case REGISTER_SUCCESS:
    case LOGIN_SUCCESS:
      return {
        ...state,
        ...payload,
        isAuthenticated: true,
        loading: false
      }
    case ACCOUNT_DELETED:
      return {
        ...state,
        token: null,
        isAuthenticated: false,
        loading: false,
        user: null
      }
    case AUTH_ERROR:
    case LOGOUT:
      return {
        ...state,
        token: null,
        isAuthenticated: false,
        loading: false,
        user: null
      }
    case AFFILIATE_REGISTER_SUCCESS:
      return {
        ...state,
        affiliateIsRegistered: payload.success,
        connectURL: payload.connectURL,
      }
    case PENDING_AFFILIATE_LOADED:
      return {
        ...state,
        pendingAffiliateIsLoaded: payload.success,
        pendingAffiliate: payload.affiliate
      }
    case UPDATE_AFFILIATE_CONNECTED_ACCOUNT:
      return {
        ...state,
        affiliateConnectedAccountUpdateLink: payload.connectURL
      }
    default:
      return state
  }
}

export default authReducer
